import React, { Component } from 'react'
import classnames from 'classnames';

export class SidebarItem extends Component {
    constructor(props) {
        super(props);

        this.state = {
            showAlternative: false
        }

        this.showAlternative = this.showAlternative.bind(this);
        this.hideAlternative = this.hideAlternative.bind(this);

        // refs
        this.itemRef = React.createRef();
    }

    showAlternative(e) {
        // console.log("show");

        this.setState({ showAlternative: true });
        // e.target.classList.add("showAlternative");

    }
    hideAlternative(e) {
        // console.log("hide");
        this.setState({ showAlternative: false });

        // e.target.classList.remove("showAlternative");
    }

    componentDidMount() {
        const { itemIndex } = this.props;

        this.itemRef.current.style.zIndex = 13 - (itemIndex + 5);
    }

    render() {
        const { item, itemIndex } = this.props;
        const { showAlternative } = this.state;

        var itemToDisplay;

        if (itemIndex === 6) {
            itemToDisplay = <li ref={this.itemRef}
                onMouseOver={function (e) { this.showAlternative(e) }.bind(this)}
                onMouseLeave={function (e) { this.hideAlternative(e) }.bind(this)}
            >
                <p className={classnames("", {
                    "glowUpItem": showAlternative
                })}>{item.name}</p>
                <p>{item.alternative}</p>
            </li>
        }
        else if (itemIndex === 7) {
            itemToDisplay = <li ref={this.itemRef}
                onMouseOver={function (e) { this.showAlternative(e) }.bind(this)}
                onMouseLeave={function (e) { this.hideAlternative(e) }.bind(this)}
            >
                <p className={classnames("matureItemTransformOrigin", {
                    "hideItem": showAlternative
                })}>{item.name}</p>
                <p className={classnames("", {
                    "showAlternative": showAlternative
                    , "glowUpMatureItem": showAlternative
                })}>{item.alternative}</p>
            </li>
        } else {
            itemToDisplay = <li ref={this.itemRef}
                onMouseOver={function (e) { this.showAlternative(e) }.bind(this)}
                onMouseLeave={function (e) { this.hideAlternative(e) }.bind(this)}
            >
                <p className={classnames("", {
                    "hideItem": showAlternative
                })}>{item.name}</p>
                <p className={classnames("", {
                    "showAlternative": showAlternative
                })}>{item.alternative}</p>
            </li>
        }

        return (
            <div className="sidebarItemDiv">
                {itemToDisplay}
            </div>
        )
    }
}

export default SidebarItem;