import React, { Component } from 'react'
import './MainSlider.css';
import classnames from 'classnames';
import ImageNavItem from './ImageNavItem';

export class MainSlider extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ showSlider: true });
        }, 2000);
        
        setTimeout(() => {
            this.setState({ showLeftArrow: true });
        }, 2500);
        setTimeout(() => {
            this.setState({ showRightArrow: true });
        }, 3000);

        
    }

    render() {

        const { showSlider, showLeftArrow, showRightArrow } = this.state;

        return (
            <div className="mainSliderDiv">

                <div className={classnames("mainImageDiv", {
                    "mainImageDivShow": showSlider
                })}>
                    <div className="mainImage"></div>
                </div>

                <div className="navigationImagesDiv">
                    <h1 className={classnames("imageNavArrow imageNavLeft", {
                        "imageNavArrowShow": showLeftArrow
                    })}>{"<"}</h1>

                    <ImageNavItem itemIndex={0} />
                    <ImageNavItem itemIndex={1} />
                    <ImageNavItem itemIndex={2} />
                    <ImageNavItem itemIndex={3} />

                    <h1 className={classnames("imageNavArrow imageNavRight", {
                        "imageNavArrowShow": showRightArrow
                    })}>{">"}</h1>
                </div>
            </div>
        )
    }
}

export default MainSlider;