import React, { Component } from 'react'
import './Sidebar.css';
import SidebarItem from './SidebarItem';
import classnames from 'classnames';

export class Sidebar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            sidebarItems: [
                {
                    name: "Maternity",
                    alternative: "Babies and Family"
                },
                {
                    name: "Wedding",
                    alternative: "Engagement"
                },
                {
                    name: "Nature",
                    alternative: "Landscape"
                },
                {
                    name: "Glamour",
                    alternative: "Fashion"
                },
                {
                    name: "Cars",
                    alternative: "Motorcycles"
                },
                {
                    name: "Fitness",
                    alternative: "Sports"
                },
                {
                    name: "Lifestyle",
                    alternative: ""
                },
                {
                    name: "Mature Content",
                    alternative: "18+"
                },
            ]
        }
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ popIn: true });
        }, 1000);
    }

    render() {

        const { sidebarItems, popIn } = this.state;

        return (
            <div className="sidebarWrapper">
                <div className={classnames("sidebarDiv", {
                    "sidebarDivPopIn": popIn
                })}>
                    <div className="logoDiv">
                        <h1 className="gregLogo">Greg</h1>
                        <h1 className="lewisLogo">Lewis</h1>
                    </div>
                    <ul className="sidebarList">
                        {sidebarItems.map(function (item, index) {
                            return <SidebarItem item={item} itemIndex={index} />

                        })}
                    </ul>
                </div>
                <div className={classnames("sidebarReflection", {
                    "sidebarReflectionPopIn": popIn
                })}></div>
            </div>
        )
    }
}

export default Sidebar;