import React, { Component } from 'react'
import classnames from 'classnames';

export class ImageNavItem extends Component {
    constructor(props) {
        super(props);

        this.state = {};
    }

    componentDidMount() {
        const { itemIndex } = this.props;

        var delay = itemIndex * 100 + 1500;

        setTimeout(() => {
            this.setState({ showItem: true });
        }, delay);

    }

    render() {

        const { showItem } = this.state;

        return (
            <div className="imageNavItemWrapper">
                <div className={classnames("imageNavItem", {
                    "imageNavItemShow": showItem
                })}></div>
                <div className={classnames("imageNavItemReflection", {
                    "imageNavItemReflectionShow": showItem
                })}></div>
            </div>
        )
    }
}

export default ImageNavItem;